/**
 * PAChoosePlan.js
 * Choose Plan view for PA
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Import Insurer Logos
import PABasic from 'assets/images/PA/PABasic.svg';
import PAEssential from 'assets/images/PA/PAEssential.svg';
import PAElite from 'assets/images/PA/PAElite.svg';

import MultiCompareView from '../Templates/MultiCompareView/MultiCompareView';

import PAEditDetail from './PAEditDetail';

import { fbTag, gaEvent, ampTag } from '../../lib/tagFunctions';

class PAChoosePlan extends Component {
  getAmpTags(selectedPlanData, planType) {
    try {
      let discount = null;
      _.map(selectedPlanData.info.discount, (data) => {
        discount = data.display;
      });
      if (_.isEmpty(discount)) {
        discount = '0%';
      }

      return ({
        type: planType,
        partner: selectedPlanData.info.insurer_slug,
        price: selectedPlanData.policyInfo[planType].net_price,
        discount,
      });
    } catch (error) {
      console.log(`Travel choose plan Buy now Amp tag failed: ${error.message}`);
    }

    return {};
  }

  render() {
    const { paFields } = this.props;

    return (
      <div className="pa-compare-view">
        <MultiCompareView
          {...this.props}
          category="pa"
          form="pa"
          initialIndex={1}
          planImages={{
            basic: PABasic,
            essential: PAEssential,
            elite: PAElite,
          }}
          editForm={{
            valuesToListen: [
              { value: 'plan_type', desc: 'For' },
              { value: 'family_members', desc: 'Members' },
              { value: 'num_children', desc: 'Children' },
              { value: 'start_date', desc: 'Start date' },
              { value: 'my_occupation', desc: 'My occupation' },
              { value: 'spouse_occupation', desc: 'Spouse\'s occupation' },
            ],
            conditions: {
              key: 'plan_type',
              values: {
                Myself: ['plan_type', 'start_date', 'my_occupation'],
                'Me & My Spouse': ['plan_type', 'start_date', 'my_occupation', 'spouse_occupation'],
                'Me & My Family': ['plan_type', 'start_date', 'num_children', 'family_members', 'my_occupation'],
              },
            },
            editDetail: <PAEditDetail isPopUp paFields={paFields} />,
          }}
          trackingTags={{
            basic: (selectedPlanData) => {
              fbTag('PPS2 - ButtonClick - PersonalAccident - ChoosePlan_BuyNow-Basic', { Product: 'PersonalAccident', Click_Text: 'BuyNow' });
              gaEvent('PersonalAccident - BuyNowBasic', {
                event_category: 'PersonalAccident',
                event_label: 'Button - PersonalAccident - BuyNowBasic',
              });
              ampTag('PABuyNowA', this.getAmpTags(selectedPlanData, 'basic'));
            },
            essential: (selectedPlanData) => {
              fbTag('PPS2 - ButtonClick - PersonalAccident - ChoosePlan_BuyNow-Essential', { Product: 'PersonalAccident', Click_Text: 'BuyNow' });
              gaEvent('PersonalAccident - BuyNowEssential', {
                event_category: 'PersonalAccident',
                event_label: 'Button - PersonalAccident - BuyNowEssential',
              });
              ampTag('PABuyNowA', this.getAmpTags(selectedPlanData, 'essential'));
            },
            elite: (selectedPlanData) => {
              fbTag('PPS2 - ButtonClick - PersonalAccident - ChoosePlan_BuyNow-Elite', { Product: 'PersonalAccident', Click_Text: 'BuyNow' });
              gaEvent('PersonalAccident - BuyNowElite', {
                event_category: 'PersonalAccident',
                event_label: 'Button - PersonalAccident - BuyNowElite',
              });
              ampTag('PABuyNowA', this.getAmpTags(selectedPlanData, 'elite'));
            },
          }}
          forceFixedOnDesktop
        />
      </div>
    );
  }
}

PAChoosePlan.defaultProps = {
};

PAChoosePlan.propTypes = {
  paFields: PropTypes.object.isRequired,
};

export default PAChoosePlan;
