/**
 * PAEditDetail.js
 * Edit Detail View for PA
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues, reset } from 'redux-form';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Avatar from 'assets/images/MainFormIcons/Avatar.svg';
import Family from 'assets/images/MainFormIcons/Family.svg';
import Chest from 'assets/images/MainFormIcons/Chest.svg';

import Button from 'components/FormsV3/Button/Button';
import Selection from '../Forms/Selection/Selection';
import Spinner from '../Forms/Spinner/Spinner';
import DropDownV2 from '../Forms/DropDown/DropDownV2';
import DatePickerV2 from '../Forms/DatePicker/DatePickerV2';

import RestrictionsPopup from '../Templates/RestrictionsPopup/RestrictionsPopup';

import { invokeSubmit } from '../../actions/editFormActions';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { setRestrictionPopup } from '../../actions/restrictionsActions';
import { validateForm, dateReValidate } from '../../lib/validators';
import { storeStringToDate, dateToString, slashedStringDateToDate } from '../../lib/dateFunctions';
import { remapFormFields } from '../../lib/helperFunctions';
import { fbTag, gaEvent, ampTag } from '../../lib/tagFunctions';

const formName = 'pa';

class PAEditDetail extends Component {
  constructor(props) {
    super(props);

    const { paFields } = this.props;

    const paData = remapFormFields(paFields.form_fields);

    this.state = {
      errorStringData: {},
      paData,
      minDate: slashedStringDateToDate(paData.start_date.min_date),
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseEdit = this.handleCloseEdit.bind(this);
    this.handleRevert = this.handleRevert.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  componentDidMount() {
    const { storeForm, setCloseCallBack } = this.props;
    this.originalValues = storeForm;

    if (setCloseCallBack) {
      setCloseCallBack(this.handleRevert);
    }
  }

  validateForm(e) {
    // e.preventDefault();
    // console.log(e);
    const { paData, minDate } = this.state;
    const {
      constants,
    } = this.props;

    const fieldsToCheck = [
      'plan_type',
      'my_occupation',
      'start_date',
    ];

    if (e.plan_type === 'Me & My Spouse') {
      fieldsToCheck.push('spouse_occupation');
    }

    if (e.plan_type === 'Me & My Family') {
      fieldsToCheck.push('family_members');
      fieldsToCheck.push('num_children');

      if (e.family_members === '2 Adults') {
        fieldsToCheck.push('spouse_occupation');
      }
    }

    const errorResult = validateForm(e, fieldsToCheck, {
      plan_type: { type: 'ARRAY', data: paData.plan_type.options },
      family_members: { type: 'ARRAY', data: paData.family_members.options },
      num_children: { type: 'ARRAY', data: _.range(paData.num_children.min, (paData.num_children.max) + 1, 1) },
      my_occupation: { type: 'ARRAY', data: constants.occupations },
      spouse_occupation: { type: 'ARRAY', data: constants.occupations },
      start_date: { type: 'DATE', start: minDate },
    });

    this.setState({
      errorStringData: errorResult,
    });

    return errorResult;
  }

  handleSubmit(e, isFirstValidate = true) {
    // e.preventDefault();
    // console.log(e);
    const {
      closePopUp,
      setInsurFormState,
      setSubmit,
      storeForm,
      submitCallback,
      history,
    } = this.props;

    const errorResult = this.validateForm(isFirstValidate ? e : storeForm);
    const isEmptyResult = _.isEmpty(errorResult);

    if (dateReValidate(
      isEmptyResult,
      isFirstValidate,
      errorResult,
      error => this.setState({ errorStringData: error }),
      () => this.handleSubmit(storeForm, false),
    )) {
      return;
    }

    if (_.isEmpty(errorResult)) {
      setSubmit();
      this.originalValues = storeForm;

      if (submitCallback) {
        submitCallback();
      } else {
        fbTag('PPS2 - ButtonClick - PersonalAccident - GetQuote', { Product: 'PersonalAccident', Click_Text: 'GetQuote' });
        gaEvent('PersonalAccident - Get Quote', {
          event_category: 'PersonalAccident',
          event_label: 'Button - PersonalAccident - Get Quote',
        });

        const spouseOccupation =
          e.plan_type === 'Me & My Spouse' ||
            (e.plan_type === 'Me & My Family' && e.family_members === '2 Adults') ?
            { 'occupation-spouse': e.spouse_occupation } : {};

        const children = e.plan_type === 'Me & My Family' ? { children: e.num_children } : {};

        ampTag('PAGetQuoteA', {
          type: e.plan_type,
          'occupation-self': e.my_occupation,
          ...spouseOccupation,
          ...children,
        });
      }

      if (closePopUp) {
        closePopUp();
      }

      setInsurFormState(FORMSTATES.CHOOSEPLAN.index);
      history.push(`/personal-accident${FORMSTATES.CHOOSEPLAN.path}`);
    }
  }

  handleRevert() {
    const { change, resetForm } = this.props;
    resetForm();
    Object.keys(this.originalValues).forEach((key) => {
      change(key, this.originalValues[key]);
    });
  }

  handleCloseEdit() {
    const { closePopUp } = this.props;
    closePopUp(this.handleRevert);
  }

  renderSpouse() {
    const { errorStringData } = this.state;
    const {
      change,
      form,
      constants,
      storeForm,
    } = this.props;

    if (
      storeForm &&
      (storeForm.plan_type === 'Me & My Spouse' ||
      (storeForm.plan_type === 'Me & My Family' && storeForm.family_members === '2 Adults'))
    ) {
      return (
        <Col className="input-padding-left" xs={12} sm={5}>
          <DropDownV2
            form={form}
            change={change}
            name="spouse_occupation"
            placeholderString="All Occupations"
            dropList={constants.occupations}
            size="medium"
            zIndex={95}
            desc="My spouse works as a"
            errorString={errorStringData.spouse_occupation}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
      );
    }

    return <Col className="input-padding-left" xs={12} sm={5} />;
  }

  renderFamily() {
    const { errorStringData, paData } = this.state;
    const {
      change,
      form,
      storeForm,
    } = this.props;

    if (storeForm && storeForm.plan_type === 'Me & My Family') {
      return (
        <Fragment>
          <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
            <img src={Family} alt="Family.svg" />
          </Col>
          <Col className="input-padding-right" xs={12} sm={5}>
            <Selection
              form={form}
              change={change}
              name="family_members"
              type="text-selection"
              noEmptyTextSelection
              options={_.map(paData.family_members.options, data => ({
                key: data, description: data,
              }))}
              sizeInSM={6}
              zIndex={98}
              desc={paData.family_members.display_name}
              errorString={errorStringData.family_members}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="input-padding-left" xs={12} sm={5}>
            <Spinner
              form={form}
              change={change}
              zIndex={97}
              name="num_children"
              unit="Children"
              min={paData.num_children.min}
              max={paData.num_children.max}
              increment={1}
              initial={paData.num_children.min}
              desc={paData.num_children.display_name || ''}
              errorString={errorStringData.num_children}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
        </Fragment>
      );
    }

    return null;
  }

  renderStartDate() {
    const { storeForm } = this.props;

    try {
      if (storeForm && storeForm.start_date) {
        const startDate = storeStringToDate(storeForm.start_date);
        const endDate = new Date(startDate);
        endDate.setFullYear(endDate.getFullYear() + 1);
        endDate.setDate(endDate.getDate() - 1);

        const startDateString = dateToString(startDate);
        const endDateString = dateToString(endDate);

        return (
          <Fragment>
            <Col className="input-padding-right" xs={12} sm={5} />
            <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2} />
            <Col className="input-padding-right pa-start-date" xs={12} sm={10}>
              <p>
                <span>I will like the plan to start from </span>
                <span><b>{startDateString}</b></span>
                <span> to </span>
                <span><b>{endDateString}</b></span>
              </p>
            </Col>
          </Fragment>
        );
      }
    } catch (error) {
      return null;
    }

    return null;
  }

  render() {
    const { errorStringData, paData, minDate } = this.state;
    const {
      handleSubmit,
      isPopUp,
      change,
      form,
      constants,
      storeForm,
    } = this.props;

    return (
      <div className="landing-form-container">
        {
          isPopUp ?
            <p className="edit-header">Edit Personal Accident Insurance Details</p>
          :
            null
        }
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Row className="input-offset" noGutters>
            <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
              <img src={Avatar} alt="Avatar.svg" style={{ marginTop: -11 }} />
            </Col>
            <Col xs={12} sm={10}>
              <Selection
                form={form}
                change={change}
                name="plan_type"
                type="text-selection"
                noEmptyTextSelection
                options={_.map(paData.plan_type.options, data => ({
                  key: data, description: data,
                }))}
                sizeInSM={4}
                zIndex={99}
                desc={paData.plan_type.display_name}
                errorString={errorStringData.plan_type}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            {this.renderFamily()}
            <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2} />
            <Col className="input-padding-right" xs={12} sm={5}>
              <DropDownV2
                form={form}
                change={change}
                name="my_occupation"
                placeholderString="All Occupations"
                dropList={constants.occupations}
                size="medium"
                zIndex={96}
                desc="I work as"
                errorString={errorStringData.my_occupation}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            {this.renderSpouse()}
            <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
              <img src={Chest} alt="Chest.svg" style={{ marginTop: -11 }} />
            </Col>
            <Col className="input-padding-right" xs={12} sm={5}>
              <DatePickerV2
                form={form}
                change={change}
                name="start_date"
                errorString={errorStringData.start_date}
                validateForm={() => this.validateForm(storeForm)}
                size="medium"
                zIndex={94}
                startDate={minDate}
                desc="Start Date"
              />
            </Col>
            {this.renderStartDate()}
          </Row>
          <Row className="align-items-center button-container" noGutters>
            {
              isPopUp ?
                <Col className="justify-content-center button" xs={12}>
                  <Row noGutters>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        className="button-left"
                        variant="medium-flat"
                        width={165}
                        height={72}
                        onClick={() => this.handleCloseEdit()}
                      >
                        <p>Cancel</p>
                      </Button>
                    </Col>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        type="submit"
                        variant="medium-flat-green"
                        width={165}
                        height={72}
                      >
                        <p>Save</p>
                      </Button>
                    </Col>
                  </Row>
                </Col>
              :
                <Col className="justify-content-center button" xs={12}>
                  <Button type="submit" variant="medium-flat-green" width={165} height={72}>
                    <p>Get Quote</p>
                  </Button>
                </Col>
            }
          </Row>
        </form>
        <RestrictionsPopup ref={node => this.resPopup = node} />
      </div>
    );
  }
}

PAEditDetail.defaultProps = {
  isPopUp: false,
  setCloseCallBack: null,
  closePopUp: null,
  submitCallback: null,
  storeForm: null,
};

PAEditDetail.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  isPopUp: PropTypes.bool,
  resetForm: PropTypes.func.isRequired,
  closePopUp: PropTypes.func,
  setInsurFormState: PropTypes.func.isRequired,
  setSubmit: PropTypes.func.isRequired,
  submitCallback: PropTypes.func,
  storeForm: PropTypes.object,
  setCloseCallBack: PropTypes.func,
  paFields: PropTypes.object.isRequired,
  constants: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(formName)(state),
  isRevert: state.editForm.isRevert,
  constants: state.insurance.constants,
});

const mapDispatchToProps = dispatch => ({
  resetForm: () => {
    dispatch(reset(formName));
  },
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  setSubmit: () => {
    dispatch(invokeSubmit());
  },
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
});

export default withRouter(reduxForm({
  form: formName,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(PAEditDetail)));
