import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { parsePAForm, getPAPlans, getValidInsurersList, clearPAData, setSelectedPlanData } from '../../actions/paActions';

import PAChoosePlan from '../../components/PA/PAChoosePlan';
import { fbTag, gaTag, ampTag } from '../../lib/tagFunctions';

// const defaultInsurers = ['msig', 'etiqa', 'tokio_marine'];

class PAChoosePlanContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHOOSEPLAN.index);

    fbTag('PPS2 - Pageview - PersonalAccident - ChoosePlan', { Product: 'PersonalAccident' });
    gaTag({
      page_title: 'PersonalAccident - Choose Plan',
      page_path: '/personal-accident/chooseplan',
    });
    ampTag('PAResultsS');
  }

  render() {
    const {
      paForm,
      getPlans,
      getInsurersList,
      clearData,
      setFormState,
      plansData,
      validInsurersList,
      paFields,
      history,
    } = this.props;

    if (!paFields) {
      return null;
    }
    return (
      <PAChoosePlan
        paFields={paFields}
        mainForm={paForm}
        parseForm={parsePAForm}
        getPlans={getPlans}
        getInsurersList={getInsurersList}
        clearData={clearData}
        setSelectedPlanData={this.props.setSelectedPlanData}
        setFormState={() => {
          setFormState(FORMSTATES.DETAILFORM.index);
          history.push(`/personal-accident${FORMSTATES.DETAILFORM.path}`);
        }}
        loadingFailBackState={() => {
          history.goBack();
        }}
        plansData={plansData}
        validInsurersList={validInsurersList}
      />
    );
  }
}

PAChoosePlanContainer.defaultProps = {
  plansData: null,
  validInsurersList: null,
};

PAChoosePlanContainer.propTypes = {
  paForm: PropTypes.object.isRequired,
  getPlans: PropTypes.func.isRequired,
  getInsurersList: PropTypes.func.isRequired,
  clearData: PropTypes.func.isRequired,
  setSelectedPlanData: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  plansData: PropTypes.array,
  validInsurersList: PropTypes.array,
  paFields: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  paFields: state.pa.paFields,
  paForm: getFormValues('pa')(state),
  plansData: state.pa.plansData,
  validInsurersList: state.pa.validInsurersList,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  getPlans: (parsedForm) => {
    dispatch(getPAPlans(parsedForm));
  },
  getInsurersList: (parsedForm) => {
    dispatch(getValidInsurersList(parsedForm));
  },
  clearData: () => {
    dispatch(clearPAData());
  },
  setSelectedPlanData: (data) => {
    dispatch(setSelectedPlanData(data));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PAChoosePlanContainer));
